@mixin font($fw, $fs, $ln) {
    font-weight: $fw;
    font-size: $fs;
    line-height: $ln;
}
.tools__title {
    @include font(500, 48px, 1.3);
    text-align: center;
    color: rgba(255, 255, 255, 0.9);
}
.tools__text {
    @include font(500, 18px, 1.67);
    text-align: center;
    color: rgba(255, 255, 255, 0.55);
}
.tools__subtitle {
    @include font(500, 24px, 1.5);
    color: rgba(255, 255, 255, 0.78);
}
.tools__name {
    @include font(400, 18px, 1.5);
    color: #FFA537;
}
.tools__subtext {
    @include font(400, 16px, 1.88);
    color: rgba(255, 255, 255, 0.78);
}
.tools__button-visit {
    @include font(600, 16px, 1.5);
    color: rgba(255, 255, 255, 0.55);
}
.tools__button-loadmore {
    @include font(500, 20px, 1.5);
    color: #FF6E30;
}
